.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0; }

.sr-only-focusable:active, .sr-only-focusable:focus {
  position: static;
  width: auto;
  height: auto;
  margin: 0;
  overflow: visible;
  clip: auto; }

.n2o-page-actions {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem; }

.n2o-disabled-page {
  pointer-events: none; }

.n2o-region-list .n2o-region-list__header {
  font-weight: bold; }
  .n2o-region-list .n2o-region-list__header h6 {
    margin: 0;
    padding-right: 0.5em;
    font-weight: bold;
    vertical-align: top;
    padding-left: 1em;
    position: relative;
    display: flex;
    align-items: center; }
    .n2o-region-list .n2o-region-list__header h6:hover, .n2o-region-list .n2o-region-list__header h6:focus {
      color: #d81b60; }
    .n2o-region-list .n2o-region-list__header h6 span {
      -webkit-transform: rotate(0deg);
      transform: rotate(0deg);
      -webkit-transition: -webkit-transform 0.2s ease-in 0s;
      transition: -webkit-transform 0.2s ease-in 0s;
      transition: transform 0.2s ease-in 0s;
      transition: transform 0.2s ease-in 0s, -webkit-transform 0.2s ease-in 0s;
      position: absolute;
      left: 0; }
  .n2o-region-list .n2o-region-list__header.active h6 span {
    -webkit-transform: rotate(90deg);
    transform: rotate(90deg); }
  .n2o-region-list .n2o-region-list__header a {
    color: #333333; }
    .n2o-region-list .n2o-region-list__header a:hover {
      text-decoration: none; }
  .n2o-region-list .n2o-region-list__header.disabled a {
    color: #999999; }

.n2o-region-list .n2o-region-list__body {
  display: none;
  padding: 0.5em 1em; }

.n2o-region-list .active {
  display: block; }

.page-item > .page-link.no-border {
  border-color: transparent !important;
  background-color: transparent !important;
  color: #163851; }

.n2o-wireframe {
  position: absolute;
  top: .5rem;
  bottom: .5rem;
  left: 0;
  right: 0;
  background: #d9edf7;
  border-radius: 1px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #31708f;
  text-transform: uppercase; }
  .n2o-wireframe.l-10 {
    background: #00b5cb; }
  .n2o-wireframe.l-20 {
    background: #00bed5; }
  .n2o-wireframe.l-30 {
    background: #00c7e0; }
  .n2o-wireframe.d-10 {
    background: #007f8e;
