    color: #546e7a; }
  .n2o-wireframe.gradient-top {
    -webkit-mask-image: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0)), to(black));
    mask-image: -webkit-gradient(linear, left top, right bottom, color-stop(bottom, left), color-stop(from(rgba(0, 0, 0, 0))), to(to(black)));
    mask-image: linear-gradient(left top, left bottom, from(rgba(0, 0, 0, 0)), to(black)); }
  .n2o-wireframe.gradient-bottom {
