.n2o-custom-toolbar {
  display: flex;
}

.n2o-data-grid-container .react-grid-Container {
    margin-bottom: 10px;
}

.n2o-custom-toolbar .react-grid-Toolbar {
  padding: 0 !important;
  margin: 0 !important;
  border: none !important;
}

.n2o-custom-toolbar .tools {
  display: flex !important;
  align-items: center !important;

  width: 100% !important;
  height: 100% !important;
}

.n2o-custom-toolbar .btn-toolbar {
  display: flex !important;
  align-items: center !important;
  order: -1 !important;

  width: auto !important;
  height: 100% !important;
}

.n2o-custom-toolbar .tools > .btn {
  padding: 0 !important;
  margin-right: -0.5rem !important;

  border: none !important;
}

.n2o-custom-toolbar .tools > .btn:hover {
  border: none !important;
}

.n2o-custom-toolbar .tools > .btn:focus {
  border: none !important;
  box-shadow: none !important;
}

.n2o-data-grid-row-selected {
  position: relative !important;
}

.n2o-data-grid-row-selected::before {
  content: '' !important;
  position: absolute !important;
  width: 3px !important;
  height: 100% !important;
  background-color: #317db5 !important;
  left: 0 !important;
  top: 0 !important;
}

.n2o-data-grid-row-selected .react-grid-Cell {
  background: rgba(0, 0, 0, 0.075) !important;;
}

.n2o-data-grid-row-selected .react-grid-Row.row-context-menu .react-grid-Cell, .n2o-data-grid-row-selected .react-grid-Row:hover .react-grid-Cell {
  background: rgba(0, 0, 0, 0.075) !important;
}

.n2o-data-grid-container  .rdg-selected {
  border: none !important;
}

.n2o-data-grid-custom-column {
  display: flex;
  flex-direction: column;
  justify-content: center;

  padding: 5px 10px;
  border-bottom: 1px solid #ddd;
}